.App {
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}

.Game {
  margin: 0 auto;
  text-align: center;
}

.Game h1 {
  font-size: 30px;
}

.GameInfo ul {
  list-style-type: none;
}

.Move button {
  -webkit-box-shadow: 2px 2px 2px #ccc;
          box-shadow: 2px 2px 2px #ccc;
  text-align: center;
  font-size: 16px;
  margin-bottom: 5px;
  height: 2.2em;
  -webkit-transition: background-color 0.3s ease;
  -o-transition: background-color 0.3s ease;
  transition: background-color 0.3s ease;
  width: 80%;
  text-decoration: none;
}

.Move button:hover {
  background-color: #F0F0F0;
  border: none;
}
.Move button:focus {
  outline: none;
}

.WinnerSquare {
  color: red;
}

#LatestMove {
  font-weight: bold;
  color: green;
}



